@import url(https://fonts.googleapis.com/css?family=Ubuntu);
/* Color Variables */

:root {
  --one: #e24e42;
  --two: #D9B310;
  --three: #EB6E80;
  --four: #008F95;
  --five: #2B2A2A;
}

/* Fonts and Imports */
body {
  background-color: rgb(255, 255, 255);
  background-image: url(/fullstacktrends/static/media/parallax.4cbbd78c.svg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.button-primary {
  color: #e24e42;
  color: var(--one);
  padding: 8px 30px;
  border-radius: 30px;
  border: 1px solid #EB6E80;
  border: 1px solid var(--three);
  margin: 0 10px
}

.button-primary:hover {
  background-color: #EB6E80;
  background-color: var(--three);
  border-color: #EB6E80;
  border-color: var(--three);
  color: white;
  text-decoration: none;
}

.chart-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.svg-flip {
  position: relative;
  top: -1px;
  -webkit-transform: rotate(180deg);
  /* Chrome and other webkit browsers */
  /* FF */
  /* Opera */
  /* IE9 */
  transform: rotate(180deg);
}
#header {
    background: #fff;
    box-shadow: 0px 10px 29px 0px rgba(0,0,0,0.35);
    padding: 20px;
    display: flex;
}

.nav-buttons{
    margin-left: auto;
    padding: 15px;
}
#newsletter .content {
    background: #F8F8FE;
    margin: -32px 0 -20px 0
}

.ml-form-embedWrapper {
    max-width: 50%!important;
    width: 50%!important;
}

#mlb2-1154434.ml-form-embedContainer .ml-form-embedWrapper {
    background: transparent!important;
}

.ml-form-embedContent h4 {
    font-family: 'Ubuntu', sans-serif!important;
    color: var(--five)!important;
    font-size: 2em!important;
    text-align: center!important;
}

.ml-form-embedContent p {
    color: rgb(107, 107, 107)!important;
    text-align: center!important;
    margin-bottom: 30px!important
}
.footer {
    background: #00252e;
}

footer .socials i {
    padding: 10px 20px 0 0;
    font-size: 40px
}

footer .socials i:hover {
    color: var(--three)
}

.credits h5 {
    margin-top: 20px;
    color: #8d8d8d;
    font-family: 'Ubuntu', sans-serif;
}
.credits li {
    list-style-type: none;
    color: #fff
}
.credits li:hover {
    color: var(--one);
}
.credits a {
    text-decoration: none;
}

.copyright {
    background-color: #000f13;
    color: #8d8d8d;
    text-align: center;
    padding: 20px
}
